import React, { useState } from "react";
import style from "./LeaveList/LeaveList.module.css";
import { useTranslation } from "react-i18next";
import { pdfShowUrlNext } from "../../../../services/BaseUrl";
import StaffLeaveMenu from "./StaffLeaveMenu";
const StaffLeaveCard = ({ leave, onRefetch }) => {
  const { t } = useTranslation();
  const [menu, setMenu] = useState("");

  var parts = leave?.reason.match(/[\s\S]{1,50}/g) || [];

  const datess = [];
  const monthss = [];
  const years = [];
  const months = [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "Jun",
    "Jul",
    "Aug",
    "Sep",
    "Oct",
    "Nov",
    "Dec",
  ];

  leave?.date?.map((d) => {
    const dDate = d.split("/")[0];
    const dMonth =
      d.split("/")[1].split("")[0] === "0"
        ? months[parseInt(d.split("/")[1], 10)]
        : months[d.split("/")[1]];
    const dYear = d.split("/")[2];

    datess.push(dDate);
    monthss.push(dMonth);
    years.push(dYear);
  });
  return (
    <>
      <div className={style.item}>
        <img src="/images/members/transfer-icon.svg" alt="" />
        <div className={style.text}>
          <h6 className={style.to}>
            {t("date")}: {[...new Set(datess)].map((d) => `${d}, `)}
            <span>
              {[...new Set(monthss)].map((d) => `${d} `)}{" "}
              {[...new Set(years)].map((d) => `${d} `)}
            </span>
          </h6>

          <div className={style.leave_card_attach}>
            <div
              className={style.datetime}
              style={{
                flexDirection: "column",
              }}
            >
              {parts?.length > 1 ? (
                <p>
                  {t("reason")} {" : "}
                  {parts[0]}...
                </p>
              ) : (
                <p>
                  {" "}
                  {t("reason")} {" : "}
                  {parts[0]}
                </p>
              )}

              {leave?.status === "Request" ? (
                <p className={style.unsolved}>
                  {" "}
                  {t("status")} {" : "}
                  {leave?.status}
                </p>
              ) : (
                <p className={style.solved}>
                  {" "}
                  {t("status")} {" : "}
                  {leave?.status}
                </p>
              )}
            </div>
            {leave?.attach && (
              <a
                href={`${pdfShowUrlNext}/${leave?.attach}`}
                target="_blank"
                rel="noopener noreferrer"
                style={{
                  textDecoration: "none",
                }}
              >
                <img
                  src="/images/members/receipt-icon.svg"
                  alt="receipt icon"
                  style={{
                    height: "1.5rem",
                  }}
                />
              </a>
            )}
          </div>
        </div>
        <img
          className={style.dots}
          src="/images/menu-dots-icon.svg"
          alt=""
          onClick={() => setMenu(leave)}
        />
      </div>
      {menu && (
        <StaffLeaveMenu
          menu={menu}
          onClose={() => setMenu("")}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default StaffLeaveCard;
